type AssetsMap = Record<string, string>

export interface AssetsOptions {
  images?: AssetsMap
}

const assets = {
  images: {} as Record<string, null | ImageBitmap>,
}

export async function loadImages(source: AssetsMap = {}) {
  const requests = [] as Promise<any>[]
  for (const key in source) {
    // resource has been loaded or is being loaded
    if (key in assets.images) continue
    assets.images[key] = null
    requests.push(fetch(source[key])
      .then(response => response.blob())
      .then(createImageBitmap)
      .then(image => assets.images[key] = image)
      .catch(() => delete assets.images[key]))
  }
  await Promise.all(requests)
  return assets.images
}

export async function loadAssets(options: AssetsOptions = {}) {
  await Promise.all([
    loadImages(options.images)
  ])
  return assets
}
